import React, { useState, useEffect } from 'react';

import LoginUI from './LoginUI';

import { useHistory } from 'react-router-dom';
import $ from 'jquery';

const Login = () => {

    let history = useHistory();

    const [ Users, setUsers ] = useState([]);
    const [ LoginProcess, setLoginProcess ] = useState( false );
    const [ LoginForm, setLoginForm ] = useState(
        {
            UserName: '',
            UserPass: ''
        }
    );

    useEffect(
        () => {

            setUsers(
                [
                    {
                        name: 'Usman',
                        password: 'Usman123'
                    },
                    {
                        name: 'Shahzad',
                        password: 'Shahzad123'
                    },
                    {
                        name: 'Admin',
                        password: 'Admin123'
                    }
                ]
            );

        }, []
    )

    const onChangeHandler = ( e ) => {

        const { name, value } = e.target;
        const values = {
            ...LoginForm,
            [name]: value
        }

        setLoginForm( values );

    }

    const onLogin = ( e ) => {

        e.preventDefault();
        setLoginProcess( true );

        let login = false;
        setTimeout(() => {
            for ( let x = 0; x < Users.length; x++ )
            {
    
                if ( LoginForm.UserName === Users[x].name && LoginForm.UserPass === Users[x].password )
                {
    
                    login = true;
                    setLoginProcess( false );
                    $(".Login .Login-Content .LoginForm").addClass('LoginFormBack');
                    setTimeout(() => {
                        $(".Login .Login-Content .WelcomeDiv").addClass('WelcomeDivBack');
                    }, 400);
                    setTimeout(() => {
                        $(".Login .Login-Content").addClass('Login-ContentBack');
                    }, 700);
                    setTimeout(() => {
                        history.push("/students/list");
                    }, 1500);
                
                }
    
            }

            if ( !login )
            {
                alert("Login Failed");
            }
        }, 1000);

    }

    return (
        <div>
            <LoginUI
                onLogin={ onLogin }
                onChangeHandler={ onChangeHandler }
                LoginForm={ LoginForm }
                LoginProcess={ LoginProcess }
            />
        </div>
    )

}

export default Login;